body{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #bae8e8;

}

.App{
    background-color: #0e0c29;
    padding: 40px 50px;
    color: white;
    min-width: 33vw;
    min-height: 75vh;
    text-align: center;
    box-shadow: 0px 0px 10px #0e0c29;
    border-radius: 5px;
}

h1{
    margin-bottom: 40px;
    letter-spacing: 3px;
}

input{
    width: 100%;
    border-radius: 20px;
    border: 0px;
    padding-left: 20px;
    height: 35px;
    color: #0e0c29;
}
input:focus{
    outline: none;
}

form button{
    margin: 20px;
    background-color: #bae8e8;
    border: 0px;
    border-radius: 15px;
    padding: 2px 10px;
    color: #0e0c29;
}

button:focus{
    outline: none;
}

ul{
    padding: 0px;
}

li {
    list-style: none;
    padding: 15px 0px;
    word-break: break-all;
    overflow: hidden;
}
li span{
    float: left;
    font-weight: 500;
}
li div{
    float: right;
}
li button{
    all: unset;
    margin: 0px 10px;
    color: #bae8e8;
}

.empty{
    padding-top: 10%;
    font-weight: 300;
    font-size: 25px;
}